"use client"

import { useEffect, useState } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import { LayoutDashboard, Users, UserPlus, Mail, Calendar, BarChart3, Settings, Briefcase, XCircle, Inbox, Link2, ChevronLeft, ChevronRight } from "lucide-react"
import { Button } from "@/components/ui/button"

export function Sidebar() {
  const pathname = usePathname()
  const [unreadCount, setUnreadCount] = useState(0)
  const [isCollapsed, setIsCollapsed] = useState(false)

  // Load collapsed state from localStorage
  useEffect(() => {
    const saved = localStorage.getItem("sidebar_collapsed")
    if (saved !== null) {
      setIsCollapsed(saved === "true")
    }
  }, [])

  const toggleSidebar = () => {
    const newState = !isCollapsed
    setIsCollapsed(newState)
    localStorage.setItem("sidebar_collapsed", String(newState))
  }

  const navigation = [
    { name: "Dashboard", href: "/", icon: LayoutDashboard },
    { name: "Opportunities", href: "/opportunities", icon: Briefcase },
    { name: "Leads", href: "/leads", icon: UserPlus },
    { name: "Not Interested", href: "/not-interested", icon: XCircle },
    { name: "Contacts", href: "/contacts", icon: Users },
    { name: "Follow-ups", href: "/follow-ups", icon: Calendar },
  { name: "Inbox", href: "/inbox", icon: Inbox },
  { name: "Email Rules", href: "/email-rules", icon: Mail },
  { name: "Templates", href: "/templates", icon: Mail },
  { name: "Email Sync", href: "/email-sync", icon: Link2 },
    { name: "Analytics", href: "/analytics", icon: BarChart3 },
  ]

  useEffect(() => {
    // Check for unread emails by getting all email IDs from localStorage or API
    // For now, we'll check localStorage directly
    const checkUnreadCount = () => {
      try {
        const status = localStorage.getItem("crm_email_read_status")
        if (status) {
          const emailStatus = JSON.parse(status)
          const unread = Object.values(emailStatus).filter((s: any) => !s.read).length
          setUnreadCount(unread)
        }
      } catch (error) {
        // Ignore errors
      }
    }

    checkUnreadCount()
    // Check periodically
    const interval = setInterval(checkUnreadCount, 5000)
    return () => clearInterval(interval)
  }, [])

  return (
    <>
      <aside className={cn(
        "fixed left-0 top-0 z-40 h-screen border-r border-border bg-card transition-all duration-300",
        isCollapsed ? "w-16" : "w-64"
      )}>
        <div className="flex h-16 items-center gap-2 border-b border-border px-4">
          {!isCollapsed && (
            <>
              <div className="flex h-8 w-8 items-center justify-center rounded-lg bg-primary">
                <span className="text-sm font-bold text-primary-foreground">CRM</span>
              </div>
              <span className="text-lg font-semibold text-foreground">Sales CRM</span>
            </>
          )}
          <Button
            variant="ghost"
            size="icon"
            className="ml-auto h-8 w-8"
            onClick={toggleSidebar}
            title={isCollapsed ? "Expand sidebar" : "Collapse sidebar"}
          >
            {isCollapsed ? <ChevronRight className="h-4 w-4" /> : <ChevronLeft className="h-4 w-4" />}
          </Button>
        </div>

        <nav className="flex flex-col gap-1 p-4">
          {navigation.map((item) => {
            const isActive = pathname === item.href
            const badge = item.name === "Inbox" ? unreadCount : undefined
            return (
              <Link
                key={item.name}
                href={item.href}
                className={cn(
                  "flex items-center gap-3 rounded-lg px-3 py-2 text-sm font-medium transition-colors relative",
                  isActive
                    ? "bg-primary text-primary-foreground"
                    : "text-muted-foreground hover:bg-secondary hover:text-foreground",
                  isCollapsed && "justify-center"
                )}
                title={isCollapsed ? item.name : undefined}
              >
                <item.icon className="h-5 w-5 shrink-0" />
                {!isCollapsed && (
                  <>
                    <span className="flex-1">{item.name}</span>
                    {badge !== undefined && badge > 0 && (
                      <span className={cn(
                        "flex h-5 min-w-5 items-center justify-center rounded-full px-1.5 text-xs font-medium",
                        isActive
                          ? "bg-primary-foreground text-primary"
                          : "bg-primary text-primary-foreground"
                      )}>
                        {badge > 99 ? "99+" : badge}
                      </span>
                    )}
                  </>
                )}
                {isCollapsed && badge !== undefined && badge > 0 && (
                  <span className="absolute -top-1 -right-1 flex h-4 w-4 items-center justify-center rounded-full bg-primary text-[10px] font-medium text-primary-foreground">
                    {badge > 9 ? "9+" : badge}
                  </span>
                )}
              </Link>
            )
          })}
        </nav>

        <div className={cn("absolute bottom-4", isCollapsed ? "left-2 right-2" : "left-4 right-4")}>
          <Link
            href="/settings"
            className={cn(
              "flex items-center gap-3 rounded-lg px-3 py-2 text-sm font-medium text-muted-foreground transition-colors hover:bg-secondary hover:text-foreground",
              isCollapsed && "justify-center"
            )}
            title={isCollapsed ? "Settings" : undefined}
          >
            <Settings className="h-5 w-5 shrink-0" />
            {!isCollapsed && <span>Settings</span>}
          </Link>
        </div>
      </aside>
      {/* Spacer to account for sidebar */}
      <div className={cn("transition-all duration-300", isCollapsed ? "ml-16" : "ml-64")} />
    </>
  )
}
